import React from 'react';

const Temp =(props)=>{
 
    return (
        <>
        <div className="list">
       
    <button onClick={()=>{
        props.onSelect(props.id);
    }}> - </button>
    <li>{props.id+1}. {props.text}</li>
    </div>
    </>
    );
}
export default Temp;